module.exports = "class NavJustified extends React.Component {\r\n  handleSelect(selectedKey) {\r\n    alert(`selected ${selectedKey}`);\r\n  }\r\n\r\n  render() {\r\n    return (\r\n      <div>\r\n        <Nav\r\n          bsStyle=\"tabs\"\r\n          justified\r\n          activeKey={1}\r\n          onSelect={key => this.handleSelect(key)}\r\n        >\r\n          <NavItem eventKey={1} href=\"/home\">\r\n            NavItem 1 content\r\n          </NavItem>\r\n          <NavItem eventKey={2} title=\"Item\">\r\n            NavItem 2 content\r\n          </NavItem>\r\n          <NavItem eventKey={3} disabled>\r\n            NavItem 3 content\r\n          </NavItem>\r\n        </Nav>\r\n        <br />\r\n        <Nav\r\n          bsStyle=\"pills\"\r\n          justified\r\n          activeKey={1}\r\n          onSelect={key => this.handleSelect(key)}\r\n        >\r\n          <NavItem eventKey={1} href=\"/home\">\r\n            NavItem 1 content\r\n          </NavItem>\r\n          <NavItem eventKey={2} title=\"Item\">\r\n            NavItem 2 content\r\n          </NavItem>\r\n          <NavItem eventKey={3} disabled>\r\n            NavItem 3 content\r\n          </NavItem>\r\n        </Nav>\r\n      </div>\r\n    );\r\n  }\r\n}\r\n\r\nrender(<NavJustified />);\r\n"


